<template><section><h1>LabelGraphics</h1>
<p><code v-pre>vc-graphics-label</code> 组件用于加载二维标签文本。需要作为 <code v-pre>vc-entity</code> 的子组件使用。</p>
<h2>示例</h2>
<h3>加载标签</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity :position="position1" :description="description" :label.sync="label1">
          <vc-graphics-label :text="description" font="20px sans-serif" :pixelOffset="pixelOffset1"></vc-graphics-label>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position1"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:label.sync</span>=<span class="hljs-string">"label1"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-label</span> <span class="hljs-attr">:text</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">font</span>=<span class="hljs-string">"20px sans-serif"</span> <span class="hljs-attr">:pixelOffset</span>=<span class="hljs-string">"pixelOffset1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-label</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">description</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,
        <span class="hljs-attr">label1</span>: {},
        <span class="hljs-attr">pixelOffset1</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">0.0</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">20</span> },
        <span class="hljs-attr">position1</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">114.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">300000.0</span> }
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 label 是否显示。</td>
</tr>
<tr>
<td>text</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code> 指定 label 文字，支持'\n'换行符。</td>
</tr>
<tr>
<td>font</td>
<td>String</td>
<td><code v-pre>'30px sans-serif'</code></td>
<td><code v-pre>optional</code> 指定 label CSS 字体。</td>
</tr>
<tr>
<td>labelStyle</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 label 绘制风格。<strong>FILL: 0, OUTLINE: 1, FILL_AND_OUTLINE: 2</strong></td>
</tr>
<tr>
<td>scale</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> 指定 label 缩放比例。</td>
</tr>
<tr>
<td>showBackground</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定 label 是否显示背景。</td>
</tr>
<tr>
<td>backgroundColor</td>
<td>Object|String|Array</td>
<td><code v-pre>[0.165, 0.165, 0.165, 0.8]</code></td>
<td><code v-pre>optional</code> 指定 label 背景颜色。</td>
</tr>
<tr>
<td>backgroundPadding</td>
<td>Object</td>
<td><code v-pre>{x: 7, y: 5}</code></td>
<td><code v-pre>optional</code> 指定 label 背景偏移量。</td>
</tr>
<tr>
<td>pixelOffset</td>
<td>Object</td>
<td><code v-pre>{x: 0, y: 0}</code></td>
<td><code v-pre>optional</code> 指定 label 像素偏移量。 <strong>结构：{ x: number, y: number }</strong></td>
</tr>
<tr>
<td>eyeOffset</td>
<td>Object</td>
<td><code v-pre>{x: 0, y: 0, z: 0}</code></td>
<td><code v-pre>optional</code> 指定 label 视角偏移量。 <strong>结构：{ x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>horizontalOrigin</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 label 水平对齐方式。<strong>CENTER: 0, LEFT: 1, RIGHT: -1</strong></td>
</tr>
<tr>
<td>verticalOrigin</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 label 垂直对齐方式。<strong>CENTER: 0, BOTTOM: 1, BASELINE: 2, TOP: -1</strong></td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 label 高度模式。<strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>fillColor</td>
<td>Object|String|Array</td>
<td><code v-pre>white</code></td>
<td><code v-pre>optional</code> 指定 label 填充颜色。</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td><code v-pre>black</code></td>
<td><code v-pre>optional</code> 指定 label 轮廓线颜色。</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> 指定 label 轮廓线宽度。</td>
</tr>
<tr>
<td>translucencyByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 label 透明度随相机距离改变的参数。<strong>结构：{ near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>pixelOffsetScaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 label 偏移量随相机距离改变的参数。<strong>结构：{ near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>scaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 label 缩放随相机距离改变的参数。<strong>结构：{ near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 label 相机距离的显示条件。<strong>结构：{ near: number, far: number }</strong></td>
</tr>
<tr>
<td>disableDepthTestDistance</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 label 的深度测试距离。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/LabelGraphics.html">LabelGraphics</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>每当更改或修改属性或子属性时触发该事件。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data () {
        return {
          description: 'Hello Vue Cesium',
          label1: {},
          pixelOffset1: { x: 0.0, y: 20},
          position1: { lng: 114.0, lat: 40.0, height: 300000.0 }
        }
      },
      methods: {
        ready (cesiumInstance) {
          const {Cesium, viewer} = cesiumInstance
        }
      }
    }
  </script>